<template>
  <div class="article-wraper">
    <div class="article-classify">
      <ul>
        <li class="active">All</li>
        <li>Vue</li>
        <li>CSS</li>
        <li>JavaScript</li>
        <li>PHP</li>
        <li>H5</li>
      </ul>
    </div>
    <article-album></article-album>
  </div>
</template>

<script>
import articleAlbum from '../common/articleAlbum'

export default {
  name: 'article',
  components: {
    articleAlbum
  }
}
</script>

<style lang="less">
.article-classify {
  padding: 10px 0;
  margin: 2% 0;
  ul li {
    display: inline-block;
    cursor: pointer;
    padding: 5px 15px;
    margin: 0 5px 5px 0;
    background: #ddd;
    color: #666;
    border-radius: 2px;
    &.active {
      background: #222;
      color: #fff;
    }
  }
}
</style>



