<template>
  <div id="app">
    <main-side>
    </main-side>

    <div class="main-contnet">
      <div class="container"><router-view></router-view></div>
    </div>
  </div>
</template>

<script>
import mainSide from './components/side/mainSide'
export default {
  name: 'app',
  created: function () {
    
  },
  components: {
    mainSide
  }
}
</script>

<style lang="less">
::-webkit-scrollbar {
  width: 14px;
  height: 14px;
}
::-webkit-scrollbar-track,
::-webkit-scrollbar-thumb {
  border-radius: 999px;
  border: 5px solid transparent;
}
::-webkit-scrollbar-thumb {
  min-height: 20px;
  background-clip: content-box;
  box-shadow: 0 0 0 5px rgba(0, 0, 0, .6) inset;
}
::-webkit-scrollbar-corner {
  background: transparent;
}
#app {
  min-height: 100%;
  margin-left: 15%;
  background: rgba(245,245,245,0.8);
}
html, body {
  width: 100%;
  min-height: 100%;
  overflow-x: hidden;
  overflow-y: auto;
  font-family: "Microsoft YaHei";
}
img {
  max-width: 100%;
  max-height: 100%;
}
.main-contnet {
  position: relative;
  padding: 15px;
  
  height: 100%;
  .container {
    
  }
}
@media screen and (max-width:768px) {
  #app {
    margin: 0;
  }
}

.text-header {
  font-size: 16px;
  font-weight: 400;
  padding: 15px 0;
  .title {
    padding-left: 10px;
    color: #000;
    display: inline-block;
    font-size: 18px;
    font-weight: 300;
    line-height: 1;
    i {
      font-size: 20px;
      margin-right: 10px;
      vertical-align: bottom;
      position: relative;
      top: 2px;
    }
  }
}
</style>



