<template>
  <div class="main-side">
    <div class="head">
      <Tooltip content="你好！" placement="right">
        <div class="avatar"><a href="https://github.com/itanhang" target="_blank"><img src="./images/avatar.jpg" width="70" height="70" alt=""></a></div>
      </Tooltip>
      <div class="title">Thang</div>
      <div class="desc">WEB前端</div>
      <div class="address">
        <Icon type="location"></Icon>深圳, China
      </div>
    </div>
    <div class="main-menu">
      <router-link to="/home">首 页</router-link>
      <router-link to="/article">文 章</router-link>
      <router-link to="/tool">工 具</router-link>
    </div>
    <div class="foot">
      <div class="icon">
        <a href="#"><Tooltip content="Thang, blog" placement="top"><Icon type="ios-information" size="16"></Icon></Tooltip></a>
        <a href="#"><Tooltip content="itanhang" placement="top"><Icon type="social-github" size="16" style="margin-top:-1px"></Icon></Tooltip></a>
        <a href="#"><Tooltip content="tanhang@outlook.com" placement="top"><Icon type="at"></Icon></Tooltip></a>
      </div>
      <div class="copyright">&copy; 2017 ITANHANG.COM</div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'mainSide',
    methods: {
    }
  };
</script>

<style lang="less">
.main-side {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  width: 15%;
  z-index: 100;
  box-sizing: border-box;
  text-align: center;
  background: url('images/bg.jpg') no-repeat;
  background-size: cover;
  color: #999;
  .head {
    padding: 10% 0;
    .title {
      font-size: 16px;
      margin: 15px 0;
      color: #eee;
    }
    .desc {
      margin-top: 10px;
      font-size: 12px;
    }
    .address {
      margin-top: 10px;
      font-size: 12px;
      color: #777;
      i {
        font-size: 16px;
        vertical-align: top;
        margin-right: 5px;
      }
    }
  }
  .avatar {
    display: inline-block;
    width: 70px;
    height: 70px;
    border: 3px solid #eee;
    border-radius: 50%;
    overflow: hidden;
  }
  .foot {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 20px;
    color: #666;
    .icon {
      font-size: 0;
      line-height: 1;
      margin-bottom: 10px;
      i {
        font-size: 15px;
        margin: 0 5px;
        vertical-align: top;
        color: #999;
      }
    }
    .copyright {
      color: #666;
    }
  }
  
}
.main-menu {
  margin-top: 10%;
  a {
    display: block;
    font-size: 14px;
    line-height: 45px;
    transition: all 0.5s;
    position: relative;
    &:hover {
      color: #bbb;
    }
    &.active {
      color: #bbb;
      background: rgba(168,33,74,0.3);
    }
  }
}
@media screen and (max-width:768px) {
  .main-side {
    width: 116px;
    left: -116px;
  }
}
</style>



